/*------------------------
 Global
 ------------------------*/
 *,
 *:before,
 *:after {
  //  -webkit-transition-duration: 0.3s;
  //  -o-transition-duration: 0.3s;
  //  transition-duration: 0.3s;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
   outline: none;
 }
 
 html,
 body {
   font-family: -apple-system, BlinkMacSystemFont, "Segoe UI",
     "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
   height: 100%;
   font-size: 14px;
   line-height: 24px;
 }
 
 img {
   max-width: 100%;
   max-height: 100%;
   display: block;
 }
 
 a {
   color: inherit;
   text-decoration: none;
 }
 
 a:hover {
  //  opacity: 0.7;
   text-decoration: none;
 }
 .btn.focus, .btn:focus {
  outline: 0;
  box-shadow: inherit;
}
 h1{
   font-weight: bold;
   font-size: 1.8rem;
 }
 
 
 .userImg{
   width: 40px;
   height: 40px;
   img{
     object-fit: cover;
     width: 100%;
   }
 }

.leftNav{
  background-color: #edf4ee;
}
.list-group,
.list-group-item{
  border: 1px solid rgb(237, 244, 238);
}
.list-group-item-first{
  background-color: #edf4ee;
}
.listGroupCustom.active{
  background-color: #edf4ee;
}
.listGroupCustom:hover{
  background-color: #edf4ee;
}
.canClick{
  cursor: pointer;
  transition-duration: .3s;
}
.canClick:hover{
  opacity: 0.5;
  transition-duration: .3s;
}
.leftNav-list-group a{
  color: #212529;
  font-weight: bold;
  padding-left: 50px;
  margin-bottom: 30px;
}
.leftNav-list-group a:hover{
  color: #378356;
  border-left: 5px solid #378356;
}
.comment-list a{
  border-bottom: 1px solid rgb(237, 244, 238);
}
.cTitle{
  font-size: 1.2rem;
  color: #212529;
  font-weight: bold;
}
.cTag,
.cName,
.cTime{
  font-size: .9rem;
}
.cName,
.cTime{
  text-align: right;
}
.cTime{
  font-weight: bold;
}
.dropdownCustom{
  font-size: 1.2rem;
  font-weight: bold;
}
.btnDownload{
  border: 1px solid #cdddcd;
  background-color: #edf4ee;
}
.btnDownload:before{
  content: 'attach_file';
  font-size: 1.6rem;
  line-height: 1rem;
  display: inline-block;
  transform: translateY(5px);
  margin-right: 10px;
  font-family: 'Material Icons';
}
.btnComment button{
  font-weight: 500;
  color: #fff;
  background-color: #378356;
}
.btnComment button:hover{
  border-color: #378356;
  color: #378356;
  background-color: #fff;
}
.commentBox{
  position: relative;
}
.enterText{
  display: none;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
  background-color: #fff;
  border: 1px solid rgb(237, 244, 238);
  box-shadow: 3px 3px 15px #e6e6e6;
  ul{
    padding: 0;
    list-style-type: none;
  }
  li{
    line-height: 1rem;
  }
  textarea{
    border:none;
  }
  textarea:focus{
    border: none
  }
}
.textFunction{
  background-color: #edf4ee;
}
.btnCommentBox{
  float: right;
}
.fontBar{
  border: none;
  font-weight: bold;
  background-color: #edf4ee;
}
